.table {
    min-width: 1100px;
    margin: 0 auto;
    border-collapse: collapse;
  }

  .th {
    font-size: 14px;
    font-weight: 1000;
    line-height: 30px;
    color: white;
    text-transform: uppercase;
    letter-spacing: 1px;
    background-color: #006666;
    border: 2px solid black;
    padding: 15px;
    text-align: center;
  }

  .tr {
    color: white;
    font-size: 35px;
    font-weight: 300;
    line-height: 30px;
    letter-spacing: 0.45px;
      background-color: #80ecff;
    }

  .td1 {
    color: #5c6975;
    font-size: 15px;
    font-weight: 300;
    line-height: 30px;
    letter-spacing: 0.45px;
    padding: 10px;
    text-align: center;
    border: 2px solid #e0e6e7;
    text-transform: capitalize;
  }